<template>
  <div>
    <p>Pattern Format</p>
    <p>
      <router-link to="/">back to the contents</router-link>
    </p>
    <br>
    <textarea v-model="source" id="source" rows="50" cols="120" placeholder="source text"></textarea>
    <textarea v-model="output" id="output" rows="50" cols="120" placeholder="output" readonly></textarea>
    <br>
    <div>
      <input value="handle" type="button" @click="handle()">
    </div>
  </div>
</template>

<script>

export default {
  name: 'PatternFormatView',
  data () {
    return {
      default_rule: [
        {
          pattern: 'Min',
          fetch: 0,
          is_date_start_time: false,
          is_number_between: false,
          is_foreach: false,
          is_ignore: true,
          template: ''
        },
        {
          pattern: 'Max',
          fetch: 2,
          is_date_start_time: false,
          is_number_between: true,
          is_foreach: false,
          is_ignore: false,
          template: '<if test="p.%sMax != null">\n    AND  &lt;= #{p.%sMax}\n</if>\n<if test="p.%sMin != null">\n    AND  >= #{p.%sMin}\n</if>'
        },
        {
          pattern: 'EndTime',
          fetch: 0,
          is_date_start_time: false,
          is_number_between: false,
          is_foreach: false,
          is_ignore: true,
          template: ''
        },
        {
          pattern: 'StartTime',
          fetch: 2,
          is_date_start_time: true,
          is_number_between: false,
          is_foreach: false,
          is_ignore: false,
          template: '<if test="p.%sStartTime != null and p.%sStartTime.length &gt; 0 and p.%sEndTime != null and p.%sEndTime.length &gt; 0">\n    AND  between CONCAT(#{p.%sStartTime}, \' 00:00:00\') and CONCAT(#{p.%sEndTime}, \' 23:59:59\')\n</if>'
        },
        {
          pattern: 'List',
          fetch: 2,
          is_date_start_time: false,
          is_number_between: false,
          is_foreach: true,
          is_ignore: false,
          template: '<if test="p.%s != null and p.%s.size &gt; 0">\n    AND  in\n    <foreach collection="p.%s" index="index" item="%i" open="(" separator="," close=")">\n        #{%i}\n    </foreach>\n</if>'
        }, {
          pattern: 'String',
          fetch: 2,
          is_date_start_time: false,
          is_number_between: false,
          is_foreach: false,
          is_ignore: false,
          template: '<if test="p.%s != null and p.%s.length &gt; 0">\n    AND  like \'%' + '$' + '{p.%s}%\'\n</if>'
        }, {
          pattern: 'private',
          fetch: 2,
          is_date_start_time: false,
          is_number_between: false,
          is_foreach: false,
          is_ignore: false,
          template: '<if test="p.%s != null">\n    AND  = #{p.%s}\n</if>'
        }],
      rules: [],
      source: '@ApiModelProperty("姓名")\nprivate String name;',
      output: ''
    }
  },
  created () {
    this.rules.push(...this.default_rule)
  },
  methods: {
    handle () {
      this.output = ''
      const sourceArr = this.source.split('\n').map(e => e.trim()).filter(e => e)
      this.pattern(sourceArr)
      navigator.clipboard.writeText(this.output).then(() => console.log('copied'))
    },
    trim_right (str, size) {
      return str.substring(0, str.length - size)
    },
    pattern (arr) {
      const useMap = new Map()
      for (let i = 0; i < arr.length; i++) {
        const e = arr[i]
        for (let j = 0; j < this.rules.length; j++) {
          const r = this.rules[j]
          if (e.includes(r.pattern)) {
            const k = this.trim_right(e.split(' ')[r.fetch], 1)
            if (useMap.get(k) === 1) {
              break
            } else {
              useMap.set(k, 1)
            }
            if (r.is_ignore) {
              break
            }
            let a
            if (r.is_date_start_time) {
              a = this.trim_right(e.split(' ')[r.fetch], 10)
            } else if (r.is_number_between) {
              a = this.trim_right(e.split(' ')[r.fetch], 4)
            } else {
              a = this.trim_right(e.split(' ')[r.fetch], 1)
            }
            if (r.is_foreach) {
              this.output = this.output + r.template.replaceAll('%s', a).replaceAll('%i', this.trim_right(a, 1)) + '\n'
            } else {
              this.output = this.output + r.template.replaceAll('%s', a) + '\n'
            }
          }
        }
      }
    }
  }
}
</script>
